body{margin:0;box-sizing:border-box;font-family:"Nanum Gothic",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{height:100vh;max-width:1200px;margin:0 auto;display:flex;flex-flow:column nowrap;justify-content:space-between}.page-content{flex-grow:1;overflow-y:auto}.login-container{display:flex;margin:0 auto;flex-flow:row wrap;justify-content:space-around;max-width:400px}.login-container p{text-align:center;width:100%}#menu-appbar a{width:100%;height:100%;text-decoration:none;color:#dc2b00}#menu-appbar a:visited{color:inherit;color:#dc2b00}a{text-decoration:none;color:#dc2b00}a:visited{color:inherit;color:#dc2b00}.span-dot{height:1.3em;width:1.3em;background-color:#394e56;border-radius:50%;display:inline-block;opacity:0.5;-webkit-transition:all ease-out 0.2s;transition:all ease-out 0.2s;justify-items:center}.span-dot.current{opacity:1;height:1.56em;width:1.56em}.span-dot:hover{cursor:pointer}.fade-in{-webkit-animation:fade-in 0.8s;animation:fade-in 0.8s}.scale-in-font{-webkit-animation:scale-in-font 0.2s;animation:scale-in-font 0.2s}@-webkit-keyframes fade-in{from{opacity:0}to{opacity:1}}@keyframes fade-in{from{opacity:0}to{opacity:1}}@-webkit-keyframes scale-in-font{from{font-size:0}to{font-size:auto}}@keyframes scale-in-font{from{font-size:0}to{font-size:auto}}

header{display:flex;justify-content:center;margin:1em auto}header h1{min-width:320px;min-height:130px;background-image:url(/static/media/MYtineraryLogo.a227b9b3.png);background-size:contain;background-repeat:no-repeat;font-size:0}.landing-container{display:flex;flex-flow:column nowrap;justify-content:space-between;padding:0 1rem;height:100%;min-height:600px}.landing-container .content-wrapper{display:flex;flex-flow:column;justify-content:space-between;align-items:center;min-height:50vh}.start-browsing{display:flex;flex-flow:column nowrap;justify-content:center;align-items:center}.start-browsing img{height:8em;width:8em}.city-card{flex:1 0 40%;min-width:100px;min-height:100px;margin:0.2rem;position:relative;background-color:#ccc}.city-card .city-card-inner{position:absolute;display:flex;height:100%;width:100%;align-items:center;justify-content:center}.destination-controls button{height:25px}

.city-card{display:block;position:relative;background-color:#ccc;overflow:hidden;border-radius:4px}.city-card img{z-index:1;position:absolute;max-width:100%;height:auto;object-fit:cover;-webkit-transition:-webkit-transform ease-out 0.2s;transition:-webkit-transform ease-out 0.2s;transition:transform ease-out 0.2s;transition:transform ease-out 0.2s, -webkit-transform ease-out 0.2s}.city-card a:visited{color:inherit}.city-card h1,.city-card h2,.city-card h3,.city-card h4{z-index:10;padding:1em;color:white;background-image:radial-gradient(rgba(57,78,86,0.2), rgba(57,78,86,0.15), rgba(57,78,86,0), rgba(57,78,86,0));font-weight:600;font-size:1.4em;text-shadow:1px 1px 1px #000}.city-card:hover img,.city-card:active img,.city-card:focus img{-webkit-transform:scale(1.2);transform:scale(1.2)}.cities-container{display:flex;flex-flow:column nowrap;align-items:center;width:100%}.cities-container .cards-container{width:100%}@media only screen and (min-width: 850px){.city-card{width:800px;margin:0 auto}.city-search{min-width:600px}.cards-container{display:flex;flex-flow:row wrap}}

.city-card{display:block}

footer{margin:0.3rem auto}footer a{opacity:0.5;-webkit-transition:opacity ease-out 0.25s;transition:opacity ease-out 0.25s}footer a:hover{opacity:1}footer a img{height:40px;width:40px}@media only screen and (min-width: 400px){footer{margin:1rem auto}footer a img{height:50px;width:50px}}

